<template>
  <div>
    <v-app-bar elevation="4" color="blue" dark fixed>
      <v-icon @click="$router.go(-1)">chevron_left</v-icon>
      <router-link to="/home">
        <v-img
          src="../assets/e-unsplash-small.jpg"
          height="30"
          width="30"
          max-width="30"
          class="ml-2"
        />
      </router-link>
      <v-icon class="ml-2" @click="dialog = true">map</v-icon>
      <v-spacer></v-spacer>
      <span>{{ title || listPageType }}</span>
      <v-spacer></v-spacer>
      <router-link to="/user-info" class="text-decoration-none">
        <v-icon v-if="showSetting">settings</v-icon>
      </router-link>
    </v-app-bar>
    <div class="d-flex">
      <div
        v-if="showBtn"
        class="d-flex"
        style="margin-top: -10px; margin-right: 5px"
      >
        <v-icon>swap_vert</v-icon>
        <v-icon>filter_alt</v-icon>
      </div>
      <v-text-field
        v-if="showSearch"
        class="mb-3"
        placeholder="請輸入關鍵字"
        v-model="search"
        outlined
        hide-details
        rounded
        dense
        color="blue"
      >
        <template #append>
          <div>
            <v-icon>search</v-icon>
          </div>
        </template>
      </v-text-field>
    </div>
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-app-bar dark color="#2196f3" fixed>
          <!-- <v-icon @click="$router.go(-1)">chevron_left</v-icon> -->
          <router-link to="/home">
            <v-img
              src="../assets/e-unsplash-small.jpg"
              height="30"
              width="30"
              max-width="30"
              class="ml-2"
            />
          </router-link>
          <v-icon class="ml-2" @click="dialog = true">map</v-icon>
          <v-spacer></v-spacer>
          <span>{{ title || listPageType }}</span>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-icon dark text @click="setCities"> close </v-icon>
          </v-toolbar-items>
        </v-app-bar>
        <v-container fluid class="mt-10">
          <!-- <v-chip-group v-model="selectCities" column multiple>
            <v-chip
              filter
              outlined
              filter-icon="check"
              color="primary"
              v-for="(city, i) in cities"
              :key="i"
            >
              {{ city }}
            </v-chip>
          </v-chip-group> -->
          <v-list>
            <v-list-item v-for="(city, i) in cities" :key="i">
              <v-list-item-content @click="addCity(city)">
                <span>{{ city.name }}</span>
              </v-list-item-content>
              <v-list-item-icon v-if="city.selected">
                <v-icon>check</v-icon>
              </v-list-item-icon>
            </v-list-item>
          </v-list>
        </v-container>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  props: {
    showBtn: {
      type: Boolean,
      default: false,
    },
    showSearch: {
      type: Boolean,
      default: true,
    },
    title: {
      type: String,
      default: "",
    },
    showSetting: {
      type: Boolean,
      default: false,
    },
  },

  data: () => ({
    search: "",
    dialog: false,
    cities: [
      { name: "臺北市", selected: false },
      { name: "新北市", selected: false },
      { name: "桃園市", selected: false },
      { name: "臺中市", selected: false },
      { name: "臺南市", selected: false },
      { name: "高雄市", selected: false },
      { name: "新竹市", selected: false },
      { name: "嘉義市", selected: false },
      { name: "新竹縣", selected: false },
      { name: "苗栗縣", selected: false },
      { name: "彰化縣", selected: false },
      { name: "南投縣", selected: false },
      { name: "雲林縣", selected: false },
      { name: "嘉義縣", selected: false },
      { name: "屏東縣", selected: false },
      { name: "宜蘭縣", selected: false },
      { name: "花蓮縣", selected: false },
      { name: "臺東縣", selected: false },
      { name: "澎湖縣", selected: false },
      { name: "金門縣", selected: false },
      { name: "連江縣", selected: false },
      { name: "基隆市", selected: false },
    ],
    selectCities: [],
  }),

  computed: {
    ...mapState(["listPageType"]),
  },

  mounted() {
    let cities = JSON.parse(localStorage.getItem("selectCities"));
    if (cities && cities.length) {
      // this.selectCities = cities;
      this.cities = cities;
    }
  },

  methods: {
    setCities() {
      this.dialog = false;
      // localStorage.setItem("selectCities", JSON.stringify(this.selectCities));
      localStorage.setItem("selectCities", JSON.stringify(this.cities));
    },
    addCity(item) {
      console.log(item);
      this.cities.map((e) => {
        if (e.name === item.name) {
          e.selected = !e.selected;
        }
        return e;
      });
    },
  },
};
</script>

<style>
.overflow {
  overflow: hidden !important;
}
</style>